
.contact-page {
    max-width: 37.5rem;
    margin: 0 auto;
    padding: 1.25rem;
    color: aliceblue;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 0.625rem;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 0.3125rem;
  }
  
  .form-group label {
    font-weight: bold;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.625rem;
    border: 0.0625rem solid #ccc;
    border-radius: 0.25rem;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .submit-button {
    align-self: center;
    padding: 0.625rem 1.25rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 0.25rem;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #0069d9;
  }
  
  .submit-button:active {
    background-color: #005cbf;
  }
  
  .status-message {
    margin-top: 0.625rem;
    text-align: center;
    font-style: italic;
  }
  