.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.25rem;
}

.babyphoto-container img {
    width: 12.5rem;
    height: 12.5rem;
    object-fit: cover;
    border-radius: 50%;
    transition: all 0.3s ease;
}

.input-container.active .babyphoto-container img {
    width: 6.25rem;
    height: 6.25rem;
}

.input-container input {
    padding: 0.625rem;
    font-size: 1rem;
    border: 0.0625rem solid #ccc;
    border-radius: 0.25rem;
    transition: all 0.3s ease;
}

.input-container.active input {
    width: 30%;
}

#result {
    display: flex;
    flex-wrap: wrap;
    gap: 0.625rem;
    justify-content: center;
    align-items: center;
}

#result p {
    margin: 0;
    padding: 0.3125rem 0.625rem;
    background-color: #f0f0f0;
    border-radius: 0.25rem;
}
